@import "../settings";
@import "../mixins";

.theme-header, .title-bar-right, .off-canvas, .vertical-menu, nav, .gh, .main-search, .breadcrumbs, .footer-icon-bar, .expanded-footer, .notwebfile {
display: none !important;
}
aside {
visibility: hidden;
}

.global-banner {
height: auto;
}

footer {
background-color: $black;
height: 1rem;
}

.help-close {
display: block;
}

h1 {
font-size: $h3-font-size;
}
h2 {
font-size: $h4-font-size;
}
h3 {
font-size: $h5-font-size;

}
