@import 'util/util';
$cdn-url: "";

//CTG Colors

$red-bright		: #e82721;
$red          	: #e82721;
$red-dark		: #c72221;
$gray-pale		: #e3ddda;
$gray-light		: #c0b9b5;
$gray-color		: #837d7a;   //	: #7d7774;
$gray-med		: #5b5755;
$gray-dark		: #44413f;
$blue-dark	    : #006699;
$blue-med		: #0091f1;
$blue           : #0063a5;
$blue-light     : #38afff;
$blue-pale		: #a8dcff;
$brown-dark		: $gray-dark;
$brown-med		: $gray-light;
$brown-light	: #ede8e5;
$yellow-dark	: #ef8c2b;
$yellow       : #e4e962;
$black			: #111111;
$black-light	: #222222;
$black-lighter	: #323232;
$white			: #ffffff;
$white-dark		: #fffefe;
$white-darker	: #f8f8f8;
$green			: #008000;
$orange			: #ff7900;
$facebook		: #3B5998;
$twitter		: #1DA1F2;
$linkedin		:  #0077B5;
$gray-close		: #797676;

//wayfinder colors

$gold			: #9e8317;
$programs		: $gold;
$indigo			: #445d92;
$taxes			: $indigo;
$turquoise		: #479ac1;
$transparency	: $turquoise;
$orange-burnt	: #c8750a;
$purchasing		: $orange-burnt;
$olive			: #64733b;
$economy		: $olive;
$crimson		: #973831;
$about			: $crimson;

//old colors
//$lighter-blue : $blue-light;
//$theme-blue   : #479ac1;
//$lightest-blue: $blue-pale;
//$darkest-gray : #1d1d1d;
//$darker-gray  : $black-lighter;
//$dark-gray    : $gray-dark;
//$gray-color    : $gray-med;
//$light-gray   : $gray-light;
//$lighter-gray : $gray-pale;
//$lightest-gray: $brown-light;
//$dark-red     : $red-dark;
//$light-black  : $black-light;
//$lighter-black: $black-lighter;
//$dark-yellow  : $yellow-dark;


$link 			: $blue-dark;
$link-hover   	: $blue-med;
$link-on-black  : $blue-light;
$link-on-black-hover   : $blue-pale;


$print-bg-color        : $white;
$print-color           : $black;

//end CTG colors

//CTG Font Family
//------------
$font-family-sans-serif: "OpenSansRegular", Helvetica, Arial, sans-serif;
$bold-font-family-sans-serif: "OpenSansBold", "Arial Black", Helvetica, Arial, sans-serif;
$semibold-font-family-sans-serif: "OpenSansSemibold", Helvetica, Arial, sans-serif;
$light-font-family-sans-serif: "OpenSansLight", Helvetica, Arial, sans-serif;
$extrabold-font-family-sans-serif: "OpenSansExtrabold", "Arial Black", Helvetica, Arial, sans-serif;
$font-family-monospace: Consolas, times, "Liberation Mono", Courier, monospace;


// We use these to define default font weights
$font-weight-light     : 300;
$font-weight-normal    : 400;
$font-weight-bold      : 700;
$font-weight-extrabold: 800;
$font-weight-regular   : 400;
$font-weight-semibold  : 600;

//end CTG font family




// 1. Global
// ---------
//$global-font-size: 100%;
$global-width: rem-calc(1200);
$global-lineheight: 1.5;
$foundation-palette: (
  primary: #2199e8,
  secondary: #777777,
  success: #3adb76,
  warning: #ffae00,
  alert: #cc4b37,
);
$light-gray: $gray-light;
$medium-gray: $gray-med;
$dark-gray: $gray-dark;

$body-background: $white;
$body-font-color: $black;
$body-font-family: $font-family-sans-serif;
$body-antialiased: false;
$global-margin: 1rem;
$global-padding: 1rem;
$global-weight-normal: 400;
$global-weight-bold: 600;
$global-radius: 0;
$global-text-direction: ltr;
//$global-flexbox: false;
$global-flexbox: true;
$print-transparent-backgrounds: true;

@include add-foundation-colors;


//$text-direction: ltr;
//$global-namespace: false;

$global-font-size: 16px;
$rem-base: 16px;

//$global-right: if($global-text-direction == rtl, left, right);
//$global-left: if($global-text-direction == rtl, right, left);

// overwrite global one

// $foundation-colors: (
//   primary: $primary-color,
//   secondary: $secondary-color,
//   success: $success-color,
//   alert: $alert-color,
//   warning: $warning-color,
// //  gray: $gray-color
// );

// 2. Breakpoints
// --------------

// $breakpoints: (
//   small: 0,
//  medium: 640px,
//   large: 1024px,
//  xlarge: 1200px,
//   xxlarge: 1440px,
// );
// $breakpoint-classes: (small medium large);

//CTG Overrides
$breakpoints: (
  small: 0,
  //small-medium: 512px,
  medium: 769px,
  large: 1024px,
  xlarge: 1200px,
  // xlarge-medium: 1300px,
  // xxlarge: 1440px,
);
$breakpoint-classes: (small medium large xlarge);


// 3. The Grid  - Using Defaults
// -----------

$grid-row-width: $global-width;
$grid-column-count: 12;
$grid-column-gutter: (
  //small: 20px,
  small: 30px,
  medium: 30px,
);
$grid-column-align-edge: true;
//$block-grid-max: 8;
$block-grid-max: 6;
//$grid-column-gutter: 30px;


// 4. Base Typography
// ------------------


//$header-font-family: $body-font-family;
//$header-font-weight: $global-weight-normal;
$header-font-family: 	$bold-font-family-sans-serif;
$header-font-weight: 	$global-weight-bold;
$header-font-style: normal;
$font-family-monospace: Consolas, 'Liberation Mono', Courier, monospace;
$header-color: inherit;
$header-lineheight: 1.4;
$header-margin-bottom: 0.5rem;

$header-styles: (
  small: (
    'h1': ('font-size': 24),
    'h2': ('font-size': 20),
    'h3': ('font-size': 19),
    'h4': ('font-size': 18),
    'h5': ('font-size': 17),
    'h6': ('font-size': 16),
  ),
	medium: (
    'h1': ('font-size': 34),
    'h2': ('font-size': 30),
    'h3': ('font-size': 25),
    'h4': ('font-size': 20),
    'h5': ('font-size': 18),
    'h6': ('font-size': 16),
  ),

);
$header-sizes: (
  small: (
    'h1': 24,
    'h2': 20,
    'h3': 19,
    'h4': 18,
    'h5': 17,
    'h6': 16,
  ),
  medium: (
    'h1': 34,
    'h2': 30,
    'h3': 25,
    'h4': 20,
    'h5': 18,
    'h6': 16,
  ),
);

// We use these to control header font sizes

$h1-font-size: 34px;
$h2-font-size: 30px;
$h3-font-size: 25px;
$h4-font-size: 20px;
$h5-font-size: 18px;
$h6-font-size: 16px;

// We use these to control header size reduction on small screens

$h1-font-reduction: 10px;
$h2-font-reduction: 10px;
$h3-font-reduction: 6px;
$h4-font-reduction: 2px;
$h5-font-reduction: 1px;
$h6-font-reduction: 0;



$header-text-rendering: optimizeLegibility;
$small-font-size: 95%;
$header-small-font-color: $gray-dark;
$paragraph-lineheight: 1.6;
$paragraph-margin-bottom: 1rem;
$paragraph-text-rendering: optimizeLegibility;
$code-color: $black;

$code-font-family: $font-family-monospace;
$code-font-weight: $global-weight-normal;
$code-background: $gray-light;
$code-border: 1px solid $gray-med;
$code-padding: rem-calc(2 5 1);
$anchor-color: $link;
$anchor-color-hover: $link-hover;
$anchor-text-decoration: none;
$anchor-text-decoration-hover: none;
$hr-width: $global-width;
$hr-border: 1px solid $gray-med;
$hr-margin: rem-calc(20) auto;
$list-lineheight: $paragraph-lineheight;
$list-margin-bottom: $paragraph-margin-bottom;
$list-style-type: disc;
$list-style-position: outside;
$list-side-margin: 1.25rem;
$list-nested-side-margin: 1.25rem;
$defnlist-margin-bottom: 1rem;
$defnlist-term-weight: $global-weight-bold;
$defnlist-term-margin-bottom: 0.3rem;
$blockquote-color: $gray-dark;
$blockquote-padding: rem-calc(9 20 0 19);
$blockquote-border: none;
$cite-font-size: rem-calc(13);
$cite-color: $gray-dark;
$cite-pseudo-content: '\2014 \0020';
$keystroke-font: $font-family-monospace;
$keystroke-color: $black;
$keystroke-background: $gray-light;
$keystroke-padding: rem-calc(2 4 0);
$keystroke-radius: $global-radius;
$abbr-underline: none;

// 5. Typography Helpers
// ---------------------

$lead-font-size: $global-font-size * 1.25;
$lead-lineheight: 1.6;
$subheader-lineheight: 1.4;
$subheader-color: $gray-dark;
$subheader-font-weight: $global-weight-normal;
//$subheader-margin-top: 0.2rem;
$subheader-margin-top: 0;
$subheader-margin-bottom: 0.5rem;
$stat-font-size: 2.5rem;

// 6. Abide
// --------


$abide-inputs: true;
$abide-labels: true;
$input-background-invalid: $alert-color;
$form-label-color-invalid: $alert-color;
$input-error-color: $alert-color;
$input-error-font-size: rem-calc(12);
$input-error-font-weight: $global-weight-bold;


// 7. Accordion
// ------------

$accordion-background: $white;
$accordion-plusminus: true;
$accordion-title-font-size: rem-calc(12);
$accordion-item-color: $black;
$accordion-item-background-hover: $gray-light;
$accordion-item-padding: 1rem 0.5rem;
$accordion-content-background: $white;
$accordion-content-border: none;
$accordion-content-color: $body-font-color;
$accordion-content-padding: 1rem;

// 8. Accordion Menu
// -----------------
$accordionmenu-arrows: true;
$accordionmenu-arrow-color: $primary-color;
$accordionmenu-arrow-size: 6px;

// 9. Badge - this is the counter by social media icons
// --------

$badge-background: $primary-color;
$badge-color: $white;
$badge-color-alt: $black;
$badge-padding: 0.3em;
$badge-minwidth: 2.1em;
$badge-font-size: 0.6rem;


// 10. Breadcrumbs
// ---------------


$breadcrumbs-margin: 0 0 $global-margin 0;
$breadcrumbs-item-font-size: rem-calc(11);
$breadcrumbs-item-color: $link;
$breadcrumbs-item-color-current: $black;
$breadcrumbs-item-color-disabled: $gray-med;
$breadcrumbs-item-margin: 0.75rem;
$breadcrumbs-item-uppercase: true;
$breadcrumbs-item-slash: true;

// 11. Button
// ----------



$button-padding: 0.85em 1em;
//$button-margin: 0 0 $global-margin 0;
$button-margin: 0 $global-margin $global-margin 0;
$button-fill: solid;
//$button-background: $primary-color;
$button-background: $blue-dark;
//$button-background-hover: scale-color($button-background, $lightness: -15%);
$button-background-hover: scale-color($button-background, $lightness: -20%);
$button-color: $white;
//$button-color-alt: $black;
$button-color-alt: $gray-med;
$button-radius: $global-radius;
$button-sizes: (
  tiny: 0.6rem,
  small: 0.75rem,
  default: 0.9rem,
  large: 1.25rem,
);
$button-opacity-disabled: 0.25;
//new below
$button-background-hover-lightness: -20%;
$button-hollow-hover-lightness: -50%;
$button-transition: background-color 0.25s ease-out, color 0.25s ease-out;


// 12. Button Group
// ----------------

$buttongroup-margin: 1rem;
$buttongroup-spacing: 1px;
$buttongroup-child-selector: '.button';
$buttongroup-expand-max: 6;
//new below
$buttongroup-radius-on-each: true;

// 13. Callout - replaces "panel"
// -----------

$callout-background: $white;
$callout-background-fade: 85%;
$callout-border: 1px solid rgba($black, 0.25);
$callout-margin: 0 0 1rem 0;
$callout-padding: 1rem;
$callout-font-color: $body-font-color;
$callout-font-color-alt: $body-background;
$callout-radius: $global-radius;
$callout-link-tint: 30%;


// 14. Card
// --------

$card-background: $white;
$card-font-color: $body-font-color;
$card-divider-background: $light-gray;
$card-border: 1px solid $light-gray;
$card-shadow: none;
$card-border-radius: $global-radius;
$card-padding: $global-padding;
$card-margin: $global-margin;

// 15. Close Button
// ----------------

$closebutton-position: right top;
$closebutton-offset-horizontal: (
  small: 0.66rem,
  medium: 1rem,
);
$closebutton-offset-vertical: (
  small: 0.33em,
  medium: 0.5rem,
);
$closebutton-size: (
  small: 1.5em,
  medium: 2em,
);
$closebutton-lineheight: 1;
$closebutton-color: $gray-dark;
$closebutton-color-hover: $black;


// 16. Drilldown
// -------------

$drilldown-transition: transform 0.15s linear;
$drilldown-arrows: true;
$drilldown-arrow-color: $primary-color;
$drilldown-arrow-size: 6px;
$drilldown-background: $white;



// 17. Dropdown
// ------------

$dropdown-padding: 1rem;
$dropdown-background: $body-background;
$dropdown-border: 1px solid $gray-med;
$dropdown-font-size: 1rem;
$dropdown-width: 300px;
$dropdown-radius: $global-radius;
$dropdown-sizes: (
  tiny: 100px,
  small: 200px,
  large: 400px,
);

// 18. Dropdown Menu
// -----------------

$dropdownmenu-arrows: true;
//new
$dropdownmenu-arrow-color: $anchor-color;
$dropdownmenu-arrow-size: 6px;
$dropdownmenu-min-width: ($global-width * .85);
$dropdownmenu-background: rgba($white, .85);
$dropdown-border: none;

// 18. Flex Video
// --------------

// $flexvideo-padding-top: rem-calc(25);
// $flexvideo-margin-bottom: rem-calc(16);
// $flexvideo-ratio: 4 by 3;
// $flexvideo-ratio-widescreen: 16 by 9;

// 19. Forms
// ---------

$fieldset-border: 1px solid $gray-med;
$fieldset-padding: rem-calc(20);
$fieldset-margin: rem-calc(18 0);
$legend-padding: rem-calc(0 3);
$form-spacing: rem-calc(16);
$helptext-color: $black;
$helptext-font-size: rem-calc(13);
$helptext-font-style: italic;
$input-prefix-color: $black;
$input-prefix-background: $gray-light;
$input-prefix-border: 1px solid $gray-med;
$input-prefix-padding: 1rem;
$form-label-color: $black;
$form-label-font-size: rem-calc(14);
$form-label-font-weight: $global-weight-normal;
$form-label-line-height: 1.4;
$select-background: #fafafa;
$select-triangle-color: #333333;
$select-radius: $global-radius;
$input-color: $gray-dark;
//new
$input-placeholder-color: $gray-med;

$input-font-family: inherit;
$input-font-size: rem-calc(16);
//new
$input-font-weight: $global-weight-normal;

$input-background: $white;
$input-background-focus: $white;
$input-background-disabled: $gray-light;
$input-border: 1px solid $gray-med;
$input-border-focus: 1px solid $gray-dark;
$input-shadow: inset 0 1px 2px rgba($black, 0.1);
$input-shadow-focus: 0 0 5px $gray-med;
$input-cursor-disabled: default;
$input-transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
$input-number-spinners: true;
$input-radius: $global-radius;
//new
$form-button-radius: $global-radius;

// 20. Label
// ---------

$label-background: $primary-color;
//$label-color: foreground($label-background);
$label-color: $white;
$label-color-alt: $black;
$label-font-size: 0.8rem;
$label-padding: 0.33333rem 0.5rem;
$label-radius: $global-radius;

// 21. Media Object
// ----------------

$mediaobject-margin-bottom: $global-margin;
$mediaobject-section-padding: $global-padding;
$mediaobject-image-width-stacked: 100%;

// 22. Menu
// --------

$menu-margin: 0;
$menu-margin-nested: 1rem;
$menu-item-padding: 0.7rem 2rem;
$menu-icon-spacing: 0.25rem;
//$menu-expand-max: 6;
//new
$menu-item-color-active: $white;
$menu-item-background-active: map-get($foundation-palette, primary);
$menu-item-background-hover: $gray-light;
$menu-border: $gray-light;


// 23. Meter
// ---------

$meter-height: 1rem;
$meter-radius: $global-radius;
$meter-background: $gray-med;
$meter-fill-good: $success-color;
$meter-fill-medium: $warning-color;
$meter-fill-bad: $alert-color;



// 24. Off-canvas
// --------------

$offcanvas-size: 250px;
$offcanvas-vertical-size: 250px;
$offcanvas-background: $gray-light;
$offcanvas-shadow: 0 0 10px rgba($black, 0.7);
$offcanvas-push-zindex: 1;
$offcanvas-overlap-zindex: 10;
$offcanvas-reveal-zindex: 1;
$offcanvas-transition-length: 0.5s;
$offcanvas-transition-timing: ease;
$offcanvas-fixed-reveal: true;
$offcanvas-exit-background: rgba($white, 0.25);
$maincontent-class: 'off-canvas-content';
$maincontent-shadow: none;

// 25. Orbit
// ---------

$orbit-bullet-background: $gray-med;
$orbit-bullet-background-active: $gray-dark;
$orbit-bullet-diameter: 1.2rem;
$orbit-bullet-margin: 0.1rem;
$orbit-bullet-margin-top: 0.8rem;
$orbit-bullet-margin-bottom: 0.8rem;
$orbit-caption-background: rgba($black, 0.5);
$orbit-caption-padding: 1rem;
$orbit-control-background-hover: rgba($black, 0.5);
$orbit-control-padding: 1rem;
$orbit-control-zindex: 10;

// 26. Pagination
// --------------

$pagination-font-size: rem-calc(14);
$pagination-margin-bottom: $global-margin;
$pagination-item-color: $black;
$pagination-item-padding: rem-calc(3 10);
$pagination-item-spacing: rem-calc(1);
$pagination-radius: $global-radius;
$pagination-item-background-hover: $gray-light;
$pagination-item-background-current: $primary-color;
//$pagination-item-color-current: foreground($pagination-item-background-current);
$pagination-item-color-current: $white;
$pagination-item-color-disabled: $gray-med;
$pagination-ellipsis-color: $black;
$pagination-mobile-items: false;
$pagination-arrows: true;

// 27. Progress Bar
// ----------------

$progress-height: 1rem;
$progress-background: $gray-med;
$progress-margin-bottom: $global-margin;
$progress-meter-background: $primary-color;
$progress-radius: $global-radius;

// 28. Responsive Embed
// --------------------

$responsive-embed-margin-bottom: rem-calc(16);
$responsive-embed-ratio: 4 by 3;
$responsive-embed-ratio-widescreen: 16 by 9;

// 29. Reveal
// ----------

$reveal-background: $white;
$reveal-width: 600px;
$reveal-max-width: $global-width;
//$reveal-offset: rem-calc(100);
$reveal-padding: $global-padding;
$reveal-border: 1px solid $gray-med;
$reveal-radius: $global-radius;
$reveal-zindex: 1005;
$reveal-overlay-background: rgba($black, 0.45);

// 30. Slider
// ----------


$slider-height: 0.5rem;
$slider-width-vertical: $slider-height;
$slider-background: $gray-light;
$slider-fill-background: $gray-med;
$slider-handle-height: 1.4rem;
$slider-handle-width: 1.4rem;
$slider-handle-background: $primary-color;
$slider-opacity-disabled: 0.25;
$slider-radius: $global-radius;
$slider-transition: all 0.2s ease-in-out;

// 31. Switch
// ----------

$switch-background: $gray-med;
$switch-background-active: $primary-color;
$switch-height: 2rem;
$switch-height-tiny: 1.5rem;
$switch-height-small: 1.75rem;
$switch-height-large: 2.5rem;
$switch-radius: $global-radius;
$switch-margin: $global-margin;
$switch-paddle-background: $white;
$switch-paddle-offset: 0.25rem;
$switch-paddle-radius: $global-radius;
$switch-paddle-transition: all 0.25s ease-out;

// 32. Table
// ---------

$table-background: $white;
$table-color-scale: 5%;
$table-border: 1px solid $brown-dark;
$table-padding: rem-calc(8 10 10);
$table-hover-scale: 2%;
$table-row-hover: darken($table-background, $table-hover-scale);
$table-row-stripe-hover: darken($brown-light, $table-color-scale);
$table-is-striped: true;
$table-striped-background: $brown-light;
$table-stripe: even;
$table-head-background: $brown-med;
$table-head-row-hover: darken($table-head-background, $table-hover-scale);
$table-foot-background: $brown-med;
$table-foot-row-hover: darken($table-foot-background, $table-hover-scale);
$table-head-font-color: $black;
$show-header-for-stacked: true;

// 33. Tabs
// --------

$tab-margin: 0;
$tab-background: $white;
$tab-color: $primary-color;
$tab-background-active: $light-gray;
$tab-active-color: $primary-color;
$tab-item-font-size: rem-calc(12);
$tab-item-background-hover: $white;
$tab-item-padding: 1.25rem 1.5rem;
$tab-expand-max: 6;
$tab-content-background: $white;
$tab-content-border: $gray-light;
$tab-content-color: $body-font-color;
$tab-content-padding: 1rem;

// 35. Title Bar
// -------------

$titlebar-background: $black;
$titlebar-color: $white;
$titlebar-padding: 0.5rem;
$titlebar-text-font-weight: bold;
$titlebar-icon-color: $white;
$titlebar-icon-color-hover: $medium-gray;
$titlebar-icon-spacing: 0.25rem;

// 36. Tooltip
// -----------

$has-tip-font-weight: $global-weight-bold;
$has-tip-border-bottom: dotted 1px $dark-gray;
$tooltip-background-color: $brown-dark;
$tooltip-color: $white;
$tooltip-padding: 0.75rem;
$tooltip-font-size: $small-font-size;
$tooltip-pip-width: 0.75rem;
$tooltip-pip-height: $tooltip-pip-width * 0.866;
//$tooltip-pip-offset: 1.25rem;
$tooltip-radius: $global-radius;

// 37. Top Bar
// -----------

$topbar-padding: 0.5rem;
$topbar-background: $gray-light;
$topbar-submenu-background: $topbar-background;
$topbar-title-spacing: 0.45rem 1rem 0 1rem;
$topbar-input-width: 200px;
$topbar-unstack-breakpoint: medium;

//$topbar-background: $black;
//$topbar-link-color: $white;


//CTG Additions

// Global Border
//-------------------

$global-border-width: 1px;
$global-border-style: solid;
$global-border-dotted-style: dotted;
$global-border-color: $light-gray;
$global-border-darker-color: $black;

// Global Banner
// -------------
//$global-banner-height: rem-calc(122);
$global-banner-height: rem-calc(77);
$global-banner-small-height: rem-calc(104);
// Main Menu
// -------------
$main-menu-top: rem-calc(80);


// Search
// -------------

$search-bar-large-height: rem-calc(60);
$search-bar-medium-height: rem-calc(60);
$search-bar-small-height: rem-calc(50);
$search-bar-min-height: rem-calc(10);

$search-bar-large-margin: rem-calc(60);
$search-bar-medium-margin: rem-calc(40);
$search-bar-small-margin:rem-calc(30);
$search-bar-large-margin-home: rem-calc(60);
//$search-bar-medium-margin-home: rem-calc(30);
$search-bar-medium-margin-home: rem-calc(40);
$search-bar-small-margin-home: rem-calc(30);

$search-bar-zero-margin: rem-calc(0);
$search-submit-width: rem-calc(80);
$search-submit-small-width: rem-calc(50);
$search-scope-large-width: rem-calc(100);
$search-scope-small-width: rem-calc(70);
$search-scope-min-width: rem-calc(65);

// Social Icon Bar
// -------------

$icon-bar-border-width: rem-calc(5);
$icon-bar-width: rem-calc(36);
$icon-font-size: $rem-base * 2;
$icon-bar-top: $global-banner-height + 2 * $search-bar-large-margin + $search-bar-large-height;
$alert-message-height: rem-calc(30);
$main-menu-height: rem-calc(47);
$wamo-banner-height: rem-calc(120);
$wamo-banner-top-margin: rem-calc(20);
$icon-bar-top-on-home: $global-banner-height + $main-menu-height + $wamo-banner-height+ $wamo-banner-top-margin - rem-calc(5); 

// Secondary Page Color Theme
// -------------
$theme-color: $black !default;
$page-theme-color: $turquoise !default;
$page-theme-header-size: rem-calc(56);
$page-theme-header-icon-size: rem-calc(80);

// Font Awesome font family name
//$font-family-fontawesome: 'Font Awesome 5 Free';
$font-family-fontawesome: 'FontAwesome';
